      border: 1px #333 solid;
      padding: 5px;
      &.active {
        background-color: #333;
        color: $base-color;
      }
    }
  }
}
#lineChart {
  height: 23vh;
}
#pieChart {
  width: 100vw;
  height: 42vh;
}
.company-date {
  display: flex;
  margin: 0 20px;
  text-align: center;
  li {
    width: 33.3333%;
    border: 1px #333 solid;
    &.active {
      background-color: #333;
